import { Form } from './common/Form';
export class OrderAddress extends Form {
    constructor(container, events) {
        super(container, events);
        this._buttons = Array.from(container.querySelectorAll('.button_alt'));
        this._buttons.forEach((element) => element.addEventListener('click', (event) => {
            const target = event.target;
            const name = target.name;
            this.setButtonClass(name);
            events.emit('payment:changed', { target: name });
        }));
    }
    setButtonClass(name) {
        this._buttons.forEach((button) => {
            if (button.name === name) {
                button.classList.add('button_alt-active');
            }
            else {
                button.classList.remove('button_alt-active');
            }
        });
    }
    set address(address) {
        this.container.elements.namedItem('address').value =
            address;
    }
}
