import { Component } from '../base/Component';
import { ensureElement, formatNumber } from '../../utils/utils';
export class Success extends Component {
    constructor(container, total, actions) {
        super(container);
        this._total = ensureElement('.order-success__description', this.container);
        this._close = ensureElement('.order-success__close', this.container);
        // Установка текста с общей суммой и добавление обработчика события клика
        this._total.textContent = 'Списано ' + formatNumber(total) + ' синапсов';
        this._close.addEventListener('click', actions.onClick);
    }
}
