import { Component } from './base/Component';
import { ensureElement, formatNumber } from '../utils/utils';
import { cardCategory } from '../utils/constants';
// Класс Card, наследуемый от Component, для создания карточки товара
export class Card extends Component {
    // Конструктор класса
    constructor(blockName, container, events) {
        super(container);
        this._title = ensureElement(`.${blockName}__title`, container);
        this._image = ensureElement(`.${blockName}__image`, container);
        this._description = container.querySelector(`.${blockName}__text`);
        this._button = container.querySelector(`.${blockName}__button`);
        this._price = ensureElement(`.${blockName}__price`, container);
        this._category = ensureElement(`.${blockName}__category`, container);
        if (this._button) {
            this._button.addEventListener('click', events.onClick);
        }
        else {
            container.addEventListener('click', events.onClick);
        }
    }
    // Сеттеры для установки значений элементов карточки
    set title(value) {
        this.setText(this._title, value);
    }
    set image(value) {
        this.setImage(this._image, value, this.title);
    }
    set description(value) {
        this.setText(this._description, value);
    }
    set price(value) {
        if (value) {
            this.setText(this._price, `${formatNumber(value)} синапсов`);
        }
        else {
            this.setText(this._price, 'Бесценно');
            if (this._button) {
                this._button.setAttribute('disabled', '');
            }
        }
    }
    // Геттер для получения значения цены
    get price() {
        return Number(this._price.textContent);
    }
    set category(value) {
        this.setText(this._category, value);
        this._category.classList.add('card__category' + cardCategory[value]);
    }
    set button(value) {
        this.setText(this._button, value);
    }
}
