import './scss/styles.scss';
import { EventEmitter } from './components/base/events';
import { ensureElement, cloneTemplate, createElement } from './utils/utils';
import { AppModel } from './components/AppData';
import { LarekAPI } from './components/LarekApi';
import { API_URL, CDN_URL } from './utils/constants';
import { Card } from './components/Card';
import { Page } from './components/Page';
import { Modal } from './components/common/Modal';
import { Basket } from './components/common/Basket';
import { BasketCard } from './components/common/BasketCard';
import { OrderAddress } from './components/Order';
import { ContactsOrder } from './components/Contacts';
import { Success } from './components/common/Success';
const cardCatalogTemplate = ensureElement('#card-catalog');
const cardBasketTemplate = ensureElement('#card-basket');
const cardPreviewTemplate = ensureElement('#card-preview');
const basketTemplate = ensureElement('#basket');
const orderTemplate = ensureElement('#order');
const contactsTemplate = ensureElement('#contacts');
const successTemplate = ensureElement('#success');
const events = new EventEmitter();
const api = new LarekAPI(CDN_URL, API_URL);
const appData = new AppModel({}, events);
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
const basket = new Basket(cloneTemplate(basketTemplate), events);
const order = new OrderAddress(cloneTemplate(orderTemplate), events);
const contacts = new ContactsOrder(cloneTemplate(contactsTemplate), events);
// Получаем лоты с сервера, заполняем модель данных о катологе
events.on('catalog:change', () => {
    page.catalog = appData.catalog.map((product) => {
        const card = new Card('card', cloneTemplate(cardCatalogTemplate), {
            onClick: () => events.emit('preview:change', product),
        });
        return card.render({
            category: product.category,
            title: product.title,
            image: product.image,
            price: product.price,
        });
    });
    page.counter = appData.getOrderedProducts().length;
});
//Получаем карточки с сервера
api
    .getProductList()
    .then(appData.setCatalog.bind(appData))
    .catch((err) => {
    console.error(err);
});
// Обработчик события изменения предварительного просмотра товара
events.on('preview:change', (item) => {
    if (item) {
        // Создание новой карточки товара
        const card = new Card('card', cloneTemplate(cardPreviewTemplate), {
            onClick: () => {
                if (appData.productOrdered(item)) {
                    events.emit('product:delete', item);
                }
                else {
                    // Эмитирование события добавления товара
                    events.emit('product:added', item);
                }
            },
        });
        // Отображение карточки товара в модальном окне
        modal.render({
            content: card.render({
                category: item.category,
                title: item.title,
                description: item.description,
                image: item.image,
                price: item.price,
                button: appData.productOrdered(item) ? 'Убрать' : 'Купить',
            }),
        });
    }
    else {
        // Закрытие модального окна, если товар не выбран
        modal.close();
    }
});
// товар добавлен в корзину
events.on('product:added', (item) => {
    appData.addProduct(item);
    modal.close();
});
// товар удален из корзины
events.on('product:delete', (item) => {
    appData.deleteProduct(item.id);
    modal.close();
});
// Открыть корзину
events.on('basket:open', () => {
    modal.render({
        content: basket.render(),
    });
});
// Изменились данные в корзине
events.on('basket:change', () => {
    page.counter = appData.getOrderedProducts().length;
    basket.items = appData.getOrderedProducts().map((product, index) => {
        const card = new BasketCard(index, cloneTemplate(cardBasketTemplate), {
            onClick: () => {
                appData.deleteProduct(product.id);
                basket.total = appData.getTotalOrder();
            },
        });
        return card.render({ title: product.title, price: product.price });
    });
    // переход к оформлению заказа
    // Открыть форму заказа
    events.on('order:open', () => {
        order.setButtonClass('');
        modal.render({
            content: order.render({
                //payment: null,
                address: '',
                valid: false,
                errors: [],
            }),
        });
    });
    events.on('contacts:open', () => {
        modal.render({
            content: contacts.render({
                phone: '',
                email: '',
                valid: false,
                errors: [],
            }),
        });
    });
    // выбрать оплату
    events.on('payment:changed', (data) => {
        appData.setPaymentMethod(data.target);
    });
    // Обновляем общую стоимость заказа в корзине
    basket.total = appData.getTotalOrder();
});
// Открыть корзину
events.on('basket:open', () => {
    modal.render({
        content: createElement('div', {}, [basket.render()]),
    });
});
// Изменилось состояние валидации формы kонтактов
events.on('formContactsErrors:change', (errors) => {
    const { email, phone } = errors;
    contacts.valid = !email && !phone;
    contacts.errors = Object.values({ phone, email })
        .filter((i) => !!i)
        .join('; ');
});
// Изменилось состояние валидации формы с адресом
events.on('formAddresErrors:change', (errors) => {
    const { payment, address } = errors;
    order.valid = !payment && !address;
    order.errors = Object.values({ payment, address })
        .filter((i) => !!i)
        .join('; ');
});
// Изменился адрес доставки
events.on('order.address:change', (data) => {
    appData.setAddress(data.value);
});
// Изменилось одно поле формы с контактами
events.on(/^contacts\..*:change/, (data) => {
    appData.setContactsField(data.field, data.value);
});
// Отправлена формы доставки
events.on('order:submit', () => {
    modal.render({
        content: contacts.render({
            phone: '',
            email: '',
            valid: false,
            errors: [],
        }),
    });
});
events.on('contacts:submit', () => {
    appData.setOrder();
    api
        .orderProducts(appData.order)
        .then((result) => {
        const success = new Success(cloneTemplate(successTemplate), appData.order.total, {
            onClick: () => {
                modal.close();
            },
        });
        modal.render({ content: success.render({}) });
        appData.resetBasket();
        order.setButtonClass('');
        events.emit('basket:change');
    })
        .catch((err) => {
        console.error(err);
    });
});
// Блокируем прокрутку страницы если открыта модалка
events.on('modal:open', () => {
    page.locked = true;
});
// ... и разблокируем
events.on('modal:close', () => {
    page.locked = false;
});
