import { Api } from './base/api';
export class LarekAPI extends Api {
    constructor(cdn, baseUrl, options) {
        super(baseUrl, options);
        this.cdn = cdn;
    }
    // запрос продукта по Id
    getProduct(id) {
        return this.get(`/product/${id}`).then((product) => (Object.assign(Object.assign({}, product), { image: this.cdn + product.image })));
    }
    // запрос списка продуктов с сервера
    getProductList() {
        return this.get(`/product`).then((data) => data.items.map((product) => (Object.assign(Object.assign({}, product), { image: this.cdn + product.image }))));
    }
    // отправка информации о заказе на сервер
    orderProducts(order) {
        return this.post('/order', order).then((data) => data);
    }
}
