import { Component } from '../base/Component';
import { ensureElement } from '../../utils/utils';
// Класс BasketCard расширяет базовый компонент и представляет карточку товара в корзине
export class BasketCard extends Component {
    constructor(idx, container, events) {
        super(container);
        this._index = ensureElement('.basket__item-index', container);
        this._title = ensureElement('.card__title', container);
        this._button = ensureElement('.card__button', container);
        this._price = ensureElement('.card__price', container);
        this.setText(this._index, idx + 1);
        this._button.addEventListener('click', events.onClick);
    }
    // Сеттер для установки названия товара
    set title(value) {
        this.setText(this._title, value);
    }
    // Сеттер для установки цены товара
    set price(value) {
        this.setText(this._price, value + ' синапсов');
    }
    // Сеттер для установки индекса товара
    set index(value) {
        this.setText(this._index, value + 1);
    }
}
